// ÂÆö‰πâ‰∏Ä‰∏™ÂáΩÊï∞ÔºåÁî®‰∫éËé∑ÂèñlocalStorage‰∏≠ÁöÑÊï∞ÊçÆÔºåÊé•Êî∂‰∏Ä‰∏™ÂèÇÊï∞keyÔºåËøîÂõûlocalStorage‰∏≠keyÂØπÂ∫îÁöÑÂÄºÔºåÈ¶ñÂÖàÂà§Êñ≠localStorage‰∏≠ÊòØÂê¶ÊúâkeyÂØπÂ∫îÁöÑÂÄºÔºåÂ¶ÇÊûúÊúâÂàôËøîÂõûÔºåÊ≤°ÊúâÂàôËøîÂõûÁ©∫Ôºå
// Ëã•ÊúâÂÄºÂàôÂ∞ÜÂÖ∂ËΩ¨Êç¢‰∏∫JSONÂØπË±°ÔºåÂà§Êñ≠ÂÖ∂ÊòØÂê¶ÊúâexpirationTimeÂ±ûÊÄßÔºåÂ¶ÇÊûúÊúâÂàôÂà§Êñ≠ÂÖ∂ÊòØÂê¶ËøáÊúüÔºåÂ¶ÇÊûúËøáÊúüÂàôËøîÂõûÁ©∫ÔºåÂê¶ÂàôËøîÂõûvalueÔºåÂ¶ÇÊûúÊ≤°ÊúâexpirationTimeÂ±ûÊÄßÂàôÁõ¥Êé•ËøîÂõûvalue„ÄÇ

// Ëé∑ÂèñlocalStorage
export const getLocalStorageItem = key => {
    if (!key) return;
    if (window.localStorage.getItem(key)) {
        let data = window.localStorage.getItem(key);
        let dataObj = JSON.parse(data);
        if (dataObj.expirationTime && new Date().getTime() > dataObj.expirationTime) {
            window.localStorage.removeItem(key);
        } else {
            return dataObj.value;
        }
    }
};

// ËÆæÁΩÆlocalStorage
// ÂÆö‰πâ‰∏Ä‰∏™ÂáΩÊï∞ÔºåÁî®‰∫éËÆæÁΩÆlocalStorageÔºåÊé•Êî∂‰∏â‰∏™ÂèÇÊï∞keyÔºåvalueÔºåexpirationTimeÔºàÂçï‰ΩçÔºöÂ∞èÊó∂ÔºâÔºåÈ¶ñÂÖàÂà§Êñ≠keyÂíåvalueÊòØÂê¶Â≠òÂú®ÔºåÂ¶ÇÊûú‰∏çÂ≠òÂú®ÂàôËøîÂõûÁ©∫Ôºå
// ÁÑ∂ÂêéÂà§Êñ≠expirationTimeÊòØÂê¶Â≠òÂú®ÔºåÂ¶ÇÊûúÂ≠òÂú®ÂàôÂ∞ÜÂÖ∂ËΩ¨Êç¢‰∏∫ÊØ´ÁßíÔºåÁÑ∂ÂêéÂ∞ÜvalueÂíåexpirationTimeÂ≠òÂÇ®Âà∞localStorage‰∏≠ÔºåÂ¶ÇÊûúexpirationTime‰∏çÂ≠òÂú®ÂàôÂè™Â≠òÂÇ®value„ÄÇ

export const setLocalStorageItem = (key, value, expirationTime) => {
    if (!key || !value) return;
    let expirationTimeMs = expirationTime ? new Date().getTime() + 1000 * 60 * 60 * expirationTime : null;
    if (expirationTimeMs) {
        let data = {
            value: value,
            expirationTime: expirationTimeMs
        };
        window.localStorage.setItem(key, JSON.stringify(data));

    } else {
        window.localStorage.setItem(key, value);
    }
}


export default {
    getLocalStorageItem,
    setLocalStorageItem
}

